<template>
  <div class="page">
   <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="修改昵称"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content change-nickname">
      <label for="nickname">
        <div class="title">昵称</div>
        <input type="text" id="nickname" class="input" placeholder="输入昵称" v-model="nickname" maxlength="8">
        <div class="bottom-line"></div>
      </label>

      <button class="button button-disabled" :class="{'can-submit': can_submit}"@click="changeNikcname()">提交</button>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .change-nickname {
    background: #FFFFFF;
    padding-top: 75px;
    padding-left: 30px;
    color: #b5b5b5;

    @include font-regular();

    .title {
      font-size: 12px;
      height: 17px;
      line-height: 17px;
    }

    .input {
      width: 200px;
      height: 33px;
      font-size: 24px;
      line-height: 33px;
      margin-bottom: 3px;
      padding: 0;

      @include font-regular();

      &::-webkit-input-placeholder {
        line-height: 33px;
        height: 33px;
        color: #DDDDDD;

        @include font-regular();
      }
    }

    .bottom-line {
      width: 200px;
      height: 1px;
      background: #DDDDDD;
      margin-bottom: 60px;
    }

    .button-disabled {
      width: 172px;
      height: 40px;
      min-height: 40px;
      line-height: 40px;
      font-size: 13px;
      color: #FFFFFF;
      font-weight: 500;
      background-color: #DDDDDD;
      border-radius: 2px;

      @include font-semibold();

      &.can-submit {
        background-color: #ef502b;
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapState, mapActions} from 'vuex'

  export default {
    components: {
      AnimateHeader
    },

    computed: {
      ...mapState('user', [
        'user_profiles'
      ])
    },

    data () {
      return {
        nickname: '',
        can_submit: false
      }
    },

    watch: {
      nickname: function (v) {
        if (v) {
          this.can_submit = true
        } else {
          this.can_submit = false
        }
      }
    },

    mounted () {
      this.nickname = this.user_profiles.nickname
    },

    methods: {
      changeNikcname () {
        if (!this.nickname || !this.can_submit) {
          return
        }
        this.updateNickname(this.nickname)
      },

      back () {
        $router.back('/user/profile')
      },

      ...mapActions('user', [
        'updateNickname'
      ])
    }
  }
</script>



